
import {Component, Vue, Watch} from 'vue-property-decorator';
import {Action, Getter, Mutation} from 'vuex-class';
import moment from 'moment';

import {config} from '@/config';
import {TripCard, ScreenTemplate} from '@/components';

@Component({
    components: {
        ScreenTemplate,
        TripCard,
    }
})
export default class UserDetails extends Vue {
    @Action('userDetails/fetchUserDetails') fetchUserDetails: Function;
    @Getter('userDetails/getDetails') userDetails: any;
    @Mutation('userDetails/setDetails') setUserDetails: Function;
    @Action('dialogs/createDialog') createDialog: Function;
    @Getter('dialogs/newDialogId') newDialogId: Function;
    @Mutation('dialogs/setNewDialogId') setNewDialogId: Function;
    @Getter('user/userId') userId: string;

    get photo() {
        return this.userDetails.user && config.getStaticUrl(this.userDetails.user.photo)
    }

    get fullName() {
        const user = this.userDetails.user;

        return `${user.name} ${user.surname}`;
    }

    get age() {
        if (!this.userDetails.user) {
            return '';
        }

        return moment().diff(this.userDetails.user.birthday, 'years');
    }

    created() {
        const id = +(this.$route.params as any).id;

        this.fetchUserDetails(id)
    }

    @Watch('newDialogId')
    handleDialogCreate() {
        this.$router.push(`/home/chat/dialog/${this.newDialogId}`);
    }

    beforeDestroy() {
        this.setNewDialogId(null);
        this.setUserDetails({user: [], trips: []})
    }

    async handleCreateDialog() {
        await this.createDialog({
            creator: this.userId,
            addressee: this.userDetails.user.userId,
        });
    }
}
