

































































import {Component, Vue} from 'vue-property-decorator';
import {Action} from "vuex-class";

@Component({})
export default class Registration extends Vue {
    @Action('user/registration') registrationUser: Function;
    userInfo = {
        name: '',
        surname: '',
        email: '',
        password: '',
        repeatPassword: '',
    };
    response = {
        error: false,
        message: ''
    };

    async handleRegistration() {
        this.response.error = false;
        const {repeatPassword, ...userInfo} = this.userInfo;

        const res = await this.registrationUser(userInfo);

        if (res.error) {
            this.response = res;
        } else {
            this.$router.push('/authenticate/login')
        }
    }
}
