
import {Component, Vue} from 'vue-property-decorator';
import {Action} from "vuex-class";

import {InputComponent} from '@/components';

@Component({
    components: {
        InputComponent,
    },
})
export default class LoginPage extends Vue {
    @Action('user/login') loginUser: Function;

    userData = {
        email: '',
        password: '',
    };
    response = {
        error: false,
        message: ''
    };

    async handleGoogleLogin() {
        // (window as any).auth2.grantOfflineAccess().then(this.handleGoogleCallback);
    }

    async handleLogin() {
        const res = await this.loginUser(this.userData);

        if (res.error) {
            this.response = res;
        } else {
            this.$router.push('/home');
        }
    }
}
