













import { Component, Vue } from 'vue-property-decorator';
import {Action, Getter} from 'vuex-class';

@Component({})
export default class DrawerComponent extends Vue {
    @Action('drawer/close') closeDrawer: Function;
    @Getter('drawer/isOpen') isDrawerOpen: boolean;
    @Getter('drawer/isVisible') isDrawerVisible: boolean;
}
