
import {Component, Vue} from 'vue-property-decorator';
import {Action, Getter, Mutation} from 'vuex-class';

import {PlaceInput, InputComponent, SelectComponent, CheckboxComponent, ScreenTemplate} from '@/components';
import {GApi} from '@/libs/gapi';
import {companionList} from '@/pages/home/pages/create-trip/constants-trip';

@Component({
    components: {
        ScreenTemplate,
        PlaceInput,
        InputComponent,
        SelectComponent,
        CheckboxComponent,
    }
})
export default class SearchTrips extends Vue {
    @Mutation('trips/setFilter') setFilter: Function;
    @Mutation('trips/setTrips') setTrips: Function;
    companionsList = companionList;
    $refs: {
        map: HTMLDivElement;
    };
    gapi = new GApi();
    form: {[key: string]: any} = {
        from: undefined,
        to: undefined,
        dateStart: undefined,
        dateEnd: undefined,
        companion: undefined,
        withChildren: undefined,
    };

    mounted() {
        const center = {lat: 49.770511, lng: 31.547726};
        const zoom = 4.5;

        this.gapi.initialize({element: this.$refs.map, center, zoom});
    }

    handleMount(key: string, input: HTMLInputElement) {
        this.gapi.initAutocomplate(input, (googlePlace: any) => {
            const place = this.gapi.formatPlace(googlePlace);
            this.form[key] = place;
            this.gapi.setMarker(key, place);
        });
    }

    handleSearch() {
        this.setFilter(this.form);
        this.setTrips([]);
        this.$router.push('/home/list')
    }
}
