
import {Component, Vue} from 'vue-property-decorator';
import {Getter, Action} from 'vuex-class';
import {config} from '@/config';

import {
    InputComponent,
    TextareaComponent,
    SelectComponent,
    CheckboxComponent,
    ScreenTemplate,
} from '@/components';
import {IUserStore} from '@/store/modules/user/user.vuex.d';

@Component({
    components: {
        ScreenTemplate,
        InputComponent,
        TextareaComponent,
        SelectComponent,
        CheckboxComponent,
    }
})
export default class ProfilePage extends Vue {
    @Getter('user/user') user: IUserStore;
    @Action('user/updateUserInfo') updateUserInfo: Function;
    @Action('user/changePhoto') changePhoto: Function;

    form: {
        [key: string]: string
    } = {
        name: '',
        surname: '',
        gender: '',
        birthday: '',
        about: '',
        email: '',
        phone: '',
        photo: '',
    };

    $refs: {
        photoRef: HTMLInputElement
    };

    genderList = [
        {value: 'MAN', title: 'Мужской'},
        {value: 'WOMAN', title: 'Женский'},
    ];

    created() {
        this.form.name = this.user.name || '';
        this.form.gender = this.user.gender || '';
        this.form.surname = this.user.surname || '';
        this.form.birthday = this.user.birthday || '';
        this.form.about = this.user.about || '';
        this.form.email = this.user.email || '';
    }

    get photo() {
        return config.getStaticUrl(this.user.photo || '');
    }

    handleSubmit() {
        const {photo, ...data} = this.form;

        console.log(photo);

        const payload = Array
            .from(Object.keys(data))
            .reduce(
                (acc, key) => data[key] !== '' ? {...acc, [key]: data[key]} : acc
                , {}
            );

        this.updateUserInfo(payload);
    }

    handleChangePhoto() {
        if (this.$refs.photoRef.files) {
            this.changePhoto({
                id: this.user.userId,
                photo: this.$refs.photoRef.files[0],
            })
        }
    }
}
