
import {Component, Prop, Vue} from 'vue-property-decorator';

@Component({})
export default class ScreenTemplate extends Vue {
    @Prop(String) title: string;
    @Prop(Function) headerRef: Function;
    @Prop(Boolean) isBack: boolean;
    @Prop(Boolean) minimal: boolean;
    @Prop(Boolean) disableHorizontalOffset?: boolean;

    handleScroll: any | null = null;
    isSmallTitle: boolean = false;
    $refs: {
        headerRef: HTMLDivElement;
    };

    mounted() {
        this.handleScroll = window.addEventListener('scroll', () => {
            if (window.pageYOffset > 40 && this.isSmallTitle === false) {
                this.isSmallTitle = true;
            }

            if (window.pageYOffset < 40 && this.isSmallTitle === true) {
                this.isSmallTitle = false;
            }
        });

        this.headerRef && this.headerRef(this.$refs.headerRef);
    }

    beforeDestroy() {
        window.removeEventListener('scroll', this.handleScroll)
    }
}
