
import {Component, Vue, Watch} from 'vue-property-decorator';
import {Action, Getter} from 'vuex-class';

import {ScreenTemplate} from '@/components';
import {IChatItem} from '@/store/modules/chat/chat.vuex.d';

@Component({
    components: {
        ScreenTemplate
    }
})
export default class ChatsList extends Vue {
    @Action('chat/fetchChats') fetchChats: Function;
    @Getter('user/userId') userId: number;
    @Getter('chat/chatsList') chatsList: Array<IChatItem>;
    @Getter('dialogs/dialogsList') dialogsList: Array<any>;

    activeTab: string = 'dialogs';

    DIALOGS_KEY = 'dialogs';
    CHATS_KEY = 'chats';

    @Watch('userId')
    onUserIdChange() {
        this.fetchChats(this.userId)
    }

    created() {
        this.userId && this.fetchChats(this.userId);
    }

    getChatImage(chat: IChatItem) {
        return chat.trip.photo;
    }

    formatName(name: string) {
        return name.split(', ')[0]
    }

    getChatTitle(chat: IChatItem) {
        return `${this.formatName(chat.trip.from.name)} - ${this.formatName(chat.trip.to.name)}`;
    }
}
