
import {Component, Prop, Vue} from 'vue-property-decorator';

@Component({})
export default class PlaceInput extends Vue {
    @Prop(String) id: string;
    @Prop(String) label: string;

    $refs: {
        input: HTMLInputElement;
    };

    mounted() {
        this.$emit('mount', this.$refs.input)
    }
}
