
import {Component, Vue} from 'vue-property-decorator';
import {Action, Getter} from 'vuex-class';
import moment from 'moment';

import {config} from '@/config';
import {TripCard, ScreenTemplate} from '@/components';

@Component({
    components: {
        TripCard,
        ScreenTemplate,
    }
})
export default class UserDetails extends Vue {
    @Getter('user/user') user: any;
    @Getter('trips/userTrips') userTrips: any;
    @Action('trips/fetchUserTrips') fetchUserTrips: Function;
    @Action('user/exit') userExit: Function;
    @Action('chat/clearData') clearChatData: Function;
    @Getter('trips/favorite') tripsFavorite: any[];

    created() {
        this.fetchUserTrips(this.user.userId);
    }

    get photo() {
        return config.getStaticUrl(this.user.photo)
    }

    get fullName() {
        return `${this.user.name} ${this.user.surname}`;
    }

    get gender() {
        const genders: {[key: string]: string} = {
            MAN: 'Мужчина',
            WOMAN: 'Женщина',
        };

        if (this.user.gender) {
            return genders[this.user.gender]
        } else {
            return null;
        }
    }

    get age() {
        if (!this.user.birthday) {
            return '';
        }

        return moment().diff(this.user.birthday, 'years');
    }

    handleExit() {
        this.userExit();
        this.clearChatData();
        this.$router.push('/authenticate');
    }
}
