
import {Component, Vue, Watch} from 'vue-property-decorator';
import {Getter, Action} from 'vuex-class';
import moment from 'moment';

import TripItem from './components/trip-item.component.vue';
import UserCard from './components/user-card.component.vue';
import {ScreenTemplate} from '@/components';
import {GApi} from "@/libs/gapi";
import {config} from '@/config';
import {companionList} from '@/pages/home/pages/create-trip/constants-trip';
import {ParticipantsStatuses} from '@/store/modules/trips';

@Component({
    components: {
        TripItem,
        UserCard,
        ScreenTemplate,
    }
})
export default class TripDetailsPage extends Vue {
    @Action('trips/fetchTripDetails') fetchTripDetails: Function;
    @Action('trips/addParticipant') addParticipant: Function;
    @Action('trips/deleteTrip') deleteTrip: Function;
    @Action('trips/removeParticipant') removeParticipant: Function;
    @Action('trips/selectParticipant') selectParticipant: Function;
    @Action('trips/unselectParticipant') unselectParticipant: Function;
    @Getter('trips/tripDetails') tripDetails: any;
    @Getter('user/userId') userId: number;

    gapi = new GApi();
    $refs: {
        toInfoMarker: HTMLDivElement;
        fromInfoMarker: HTMLDivElement;
        mapDetails: HTMLInputElement;
    };
    getStaticPath = config.getStaticUrl;

    created() {
        this.fetchTripDetails(+(this.$route.params as any).id);
    }

    mounted() {
        const element = this.$refs.mapDetails;
        const center = {lat: 49.770511, lng: 31.547726};
        const zoom = 4.5;

        this.gapi.initialize({element, center, zoom, disableDefaultUI: true})
    }

    @Watch('tripDetails')
    onTripLoad() {
        if (this.tripDetails.from && this.tripDetails.to) {
            const from = this.getPlace(this.tripDetails.from);
            const to = this.getPlace(this.tripDetails.to);

            this.gapi.setMarker('from', from, this.$refs.fromInfoMarker);
            this.gapi.setMarker('to', to, this.$refs.toInfoMarker);
            this.gapi.drawRoute(from.location, to.location);
        }
    }

    get photo() {
        return this.tripDetails && this.tripDetails.photo;
    }

    get companion() {
        if (this.tripDetails.companion) {
            const a = companionList.find((item: any) => item.value === this.tripDetails.companion);

            return a && a.title
        } else {
            return '';
        }
    }

    get isAuthor() {
        if (!this.tripDetails.author) {
            return false;
        }

        return this.tripDetails.author.userId === this.userId;
    }

    getTime(timestamp: number) {
        return moment(timestamp).format('D MMM')
    }

    get members() {
        if (this.tripDetails.participants) {
            return this.tripDetails.participants.filter((item: any) => item.status === ParticipantsStatuses.SELECTED);
        } else {
            return [];
        }
    }

    get waiting() {
        if (!this.tripDetails.participants) {
            return [];
        }

        return this.tripDetails.participants.filter((item: any) => item.status === ParticipantsStatuses.WAITING);
    }

    handleClick() {
        if (!this.tripDetails.tripId) {
            return;
        }

        const payload = {
            tripId: this.tripDetails.tripId,
            userId: this.userId
        };

        if (this.isMember()) {
            this.removeParticipant(payload)
        } else {
            this.addParticipant(payload)
        }
    }

    async handleDeleteTrip() {
        await this.deleteTrip(this.tripDetails.tripId);
        this.$router.push(`/home/`)
    }

    handleUserClick(user: any) {
        this.$router.push(`/home/user-details/${user.userId}`)
    }

    getPlaceName(place: any) {
        return place && place.name.split(', ')[0];
    }

    isMember() {
        return this.tripDetails.participants &&
            this.tripDetails.participants
                .map((participant: any) => participant.user.userId)
                .includes(this.userId);
    }

    getPlace(place: any) {
        return {
            name: place.name,
            location: {
                lng: place.longitude,
                lat: place.latitude,
            },
        }
    }
}
