
import {Component, Prop, Vue} from 'vue-property-decorator';
import moment from 'moment';
import {Getter, Action} from 'vuex-class';

import {config} from '@/config';

@Component({})
export default class TripCard extends Vue {
    @Prop(Object) trip: any;
    @Getter('trips/favorite') tripsFavorite: any[];
    @Getter('user/userId') userId: number;
    @Action('trips/addFavorite') addFavorite: Function;
    @Action('trips/removeFavorite') removeFavorite: Function;

    get name() {
        return `${this.trip.from.name.split(',')[0].trim()} - ${this.trip.to.name.split(',')[0].trim()}`;
    }

    get isFavorite() {
        return this.tripsFavorite.map((item: any) => item.tripId).includes(this.trip.tripId)
    }

    handleFavorite() {
        const payload = {
            userId: this.userId,
            tripId: this.trip.tripId
        };

        this.isFavorite ? this.removeFavorite(payload) : this.addFavorite(payload);
    }

    get day() {
        return moment(this.trip.dateStart).format('DD');
    }

    get month() {
        return moment(this.trip.dateStart).format('MMM');
    }

    get starts() {
        return moment().to(this.trip.dateStart)
    }

    get going() {
        return this.trip.participants.slice(0, 3)
    }

    photoLink(photo: string) {
        return config.staticHost + '/' + photo;
    }
}
