
import {Component, Vue} from 'vue-property-decorator';
import {Getter, Action} from 'vuex-class';

import {TripDate, UserCard} from '@/components';

@Component({
    components: {
        TripDate,
        UserCard,
    }
})
export default class UserTrips extends Vue {
    @Getter('user/user') user: any;
    @Getter('user/userId') userId: number;
    @Getter('user/avatar') userAvatar: string;
    @Getter('user/fullName') fullName: string;
    @Getter('user/place') userPlace: string;
    @Getter('user/age') userAge: string;
    @Getter('trips/userTrips') userTrips: string;
    @Action('trips/fetchUserTrips') fetchUserTrips: Function;

    created() {
        this.fetchUserTrips(this.userId);
    }

    get cardUser() {
        if (!this.user) {
            return null;
        }

        const {name, surname, photo, birthday} = this.user;

        return {name, surname, photo, birthday};
    }
}
