
import {Component, Prop, Watch, Vue} from 'vue-property-decorator';

@Component({})
export default class MessageInput extends Vue {
    @Prop(String) value: string;
    rows: number = 1;

    $refs: {
        hiddenMessageRef: HTMLDivElement;
        inputRef: HTMLInputElement;
        inputContainerRef: HTMLDivElement;
    };

    @Watch('value')
    handleValueChange() {
        this.updateTextareaRowsCount()
    }

    updateHeight() {
        this.$emit('update-height', this.$refs.inputContainerRef.clientHeight);
    }

    mounted() {
        this.updateHeight();
    }

    updateTextareaRowsCount() {
        const rows = Math.ceil(
            (this.$refs.hiddenMessageRef.clientWidth + 15) / this.$refs.inputRef.clientWidth
        );

        if (rows > 5) {
            this.rows = 5;
        }

        if (this.rows !== rows && rows !== 0) {
            this.rows = rows;
        }

        setTimeout(() => this.updateHeight, 10)
    }
}
