
import {Component, Vue} from 'vue-property-decorator';
import {Action, Getter, Mutation} from 'vuex-class';

import {config} from '@/config';
import {companionList} from '../create-trip/constants-trip';
import {
    ModalComponent,
    InputComponent,
    SelectComponent,
    CheckboxComponent,
    PlaceInput,
    TripCard,
    ScreenTemplate,
} from '@/components'

@Component({
    components: {
        ModalComponent,
        InputComponent,
        SelectComponent,
        CheckboxComponent,
        PlaceInput,
        TripCard,
        ScreenTemplate,
    }
})
export default class ListTrips extends Vue {
    @Action('trips/fetchTrips') fetchTrips: Function;
    @Mutation('trips/setFilter') setFilter: Function;
    @Action('trips/fetchFilteredTrips') fetchFilteredTrips: Function;
    @Getter('trips/trips') trips: any;
    @Getter('trips/filter') filter: any;
    @Getter('user/photo') userPhoto: string;

    companionsList = companionList;

    get photo() {
        return config.getStaticUrl(this.userPhoto)
    }

    async created() {
        await this.fetchTrips(this.filter);
    }

    handleFilterClear() {
        this.setFilter(null);
        this.fetchTrips();
    }
}
