
import {Component, Vue} from 'vue-property-decorator';
import {Action, Getter} from 'vuex-class';
import {companionList} from './constants-trip';
import {GApi} from '@/libs/gapi';
import {ICreateTripInput} from '@/store/modules/trips';
import {
    PlaceInput,
    InputComponent,
    TextareaComponent,
    SelectComponent,
    CheckboxComponent,
    IPlace,
    ScreenTemplate,
} from '@/components';

interface IForm extends ICreateTripInput {
    [key: string]: string | IPlace | boolean | number | undefined;
}

@Component({
    components: {
        PlaceInput,
        InputComponent,
        TextareaComponent,
        SelectComponent,
        CheckboxComponent,
        ScreenTemplate,
    }
})
export default class CreateTrip extends Vue {
    @Action('trips/createTrip') createTrip: Function;
    @Getter('user/userId') userId: number;
    companionsList = companionList;

    gapi = new GApi();
    form: IForm = {
        local: true,
        from: {name: ''},
        to: {name: ''},
        dateStart: '',
        dateEnd: '',
        description: 'Описание',
        companion: companionList[0].value,
        withChildren: false,
        wishes: 'Пожелания'
    };

    mounted() {
        const element = this.$refs.createTripMap as HTMLElement;
        const center = {lat: 49.770511, lng: 31.547726};
        const zoom = 4.5;

        this.gapi.initialize({element, center, zoom});
    }

    drawRoute() {
        const from = this.form.from as IPlace;
        const to = this.form.to as IPlace;

        if (from.location && to.location) {
            this.gapi.drawRoute(from.location, to.location);
        }
    }

    handleAddMarker(id: string) {
        setTimeout(() => {
            this.gapi.setMarker(id, this.form[id] as IPlace);
            this.drawRoute();
        }, 0);
    }

    async handleSubmit() {
        await this.createTrip({...this.form, author: this.userId});

        setTimeout(() => this.$router.push('/home/list'), 500);
    }

    handleMount(key: string, input: HTMLInputElement) {
        this.gapi.initAutocomplate(input, (googlePlace: any) => {
            const place = this.getPlace(googlePlace);
            this.form[key] = place;
            this.gapi.setMarker(key, place);
        });
    }

    getPlace(googlePlace: any): IPlace {
        const name = [
            googlePlace.address_components[0] && googlePlace.address_components[0].long_name,
            googlePlace.address_components[2] && googlePlace.address_components[2].long_name,
            googlePlace.address_components[3] && googlePlace.address_components[3].long_name,
        ].join(', ');

        return {
            name,
            location: {
                lat: googlePlace.geometry.location.lat(),
                lng: googlePlace.geometry.location.lng(),
            }
        }
    }
}
