
import {Component, Vue, Watch} from 'vue-property-decorator';
import {Action, Getter} from 'vuex-class';
import {DrawerComponent, DrawerHeader, DrawerMenu, DrawerMenuItem} from '@/components/drawer';
import {BottomBar} from '@/features/bottom-bar';

import plusImage from './assets/plus-icon.svg';
import allTripsImage from './assets/world.svg';
import messageImage from './assets/mail.svg';
import searchImage from './assets/search.svg';

@Component({
    components: {
        DrawerComponent,
        DrawerHeader,
        DrawerMenu,
        DrawerMenuItem,
        BottomBar,
    }
})
export default class Home extends Vue {
    @Action('trips/fetchFavoriteTrips') fetchFavoriteTrips: Function;
    @Action('user/fetchUser') fetchUser: Function;
    @Action('chat/fetchChats') fetchChats: Function;
    @Action('dialogs/fetchDialogs') fetchDialogs: Function;
    @Action('chat/subscriptionOnNewMessages') subscriptionOnNewMessages: Function;
    @Action('chat/subscriptionOnNewChats') subscriptionOnNewChats: Function;
    @Action('dialogs/subscriptionOnNewDialogs') subscriptionOnNewDialogs: Function;
    @Action('dialogs/subscriptionOnNewDialogMessages') subscriptionOnNewDialogMessages: Function;
    @Getter('user/fullName') name: string;
    @Getter('user/userId') userId: number;
    @Getter('user/email') email: string;
    @Getter('chat/chatsIds') chatsIds: string;
    @Getter('bottomBar/open') bottomBarOpen: boolean;

    items = [
        {key: 'list', icon: allTripsImage, path: '/home/list'},
        {key: 'create', icon: plusImage, path: '/home/create-trip'},
        {key: 'search', icon: searchImage, path: '/home/search-trips'},
        {key: 'messages', icon: messageImage, path: '/home/chat/list'},
    ];

    async created() {
        this.fetchUser();
    }

    @Watch('userId')
    onUserFetched() {
        this.fetchChats(this.userId);
        this.fetchDialogs(this.userId);
        this.subscriptionOnNewChats(this.userId);
        this.subscriptionOnNewDialogs(this.userId);
        this.subscriptionOnNewDialogMessages(this.userId);
        this.fetchFavoriteTrips(this.userId);
    }

    @Watch('chatsIds')
    onChatsFetched() {
        this.subscriptionOnNewMessages(this.chatsIds);
    }

    handleMenuClick({path}: {path: string}) {
        this.$router.push(path);
    }
}
