
import {Component, Vue, Prop, Watch} from 'vue-property-decorator';

import {ScreenTemplate} from '@/components';
import {MessageComponent, MessageInput} from './';

@Component({
    components: {
        ScreenTemplate,
        MessageComponent,
        MessageInput,
    }
})
export default class MessagesPage extends Vue {
    @Prop(String) title: string;
    @Prop(Array) messages: any[];
    @Prop(Function) isAuthor: Function;
    @Prop(Function) getTimestamp: Function;
    @Prop(Function) getName: Function;
    @Prop(Function) getData: Function;

    rows = 1;
    inputMessage: string = '';

    $refs: {
        messagesListRef: HTMLDivElement;
        messagesList: HTMLUListElement;
        inputContainerRef: HTMLDivElement;
    };
    headerHeight: number = 0;
    inputHeight: number = 47;
    containerHeight: string = '';

    mounted() {
        setTimeout(() => {
            this.updateScroll();
        }, 200)
    }

    updateScroll() {
        this.$refs.messagesListRef.scrollTo(0, this.$refs.messagesListRef.scrollHeight);
    }

    handleSend() {
        if (this.inputMessage.trim().length === 0) {
            return;
        }

        this.$emit('send', this.inputMessage);

        this.inputMessage = '';
    }

    updateContainerHeight() {
        this.containerHeight = `calc(100vh - ${this.headerHeight + this.inputHeight + 25}px)`;
    }

    handleHeaderRef(element: HTMLElement) {
        this.headerHeight = element.clientHeight;
        this.updateContainerHeight()
    }

    @Watch('messages')
    handleMessagesChange() {
        setTimeout(() => this.updateScroll(), 10)
    }
}
